.navigation {
  display: flex;
  flex-direction: column;
  align-items: center;

  @include viewport--lg {
    flex-direction: row;
    flex-wrap: wrap;
    width: auto;
  }
}

.navigation__link {
  font-size: 16px;
  line-height: 28px;
  font-weight: 400;
  color: $color-white;
  padding: 10px 10px;

  @include link-reset;
  position: relative;

  @include viewport--md {
    padding: 14px 10px;
  }
}

.navigation__item:not(:last-child) {
  margin-bottom: 11px;

  @include viewport--md {
    margin-right: 26px;
    margin-bottom: 0;
  }
}

.navigation__link--current {
  &::after {
    content: "";
    width: 300px;
    height: 1px;
    background-color: #00c5ff;
    display: block;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }

  @include viewport--md {
    &::after {
      content: "";
      width: 100%;
      height: 1px;
      background-color: #00c5ff;
      display: block;
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }
}
